@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;900&family=Roboto:wght@300;400;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;900&family=Roboto:wght@300;400;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: #282c34;
  min-height: 150vh;
  font-family: 'Roboto', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: default;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear forwards;
            animation: App-logo-spin infinite 20s linear forwards;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;
  font-size: calc(80px + 2vmin);
  color: white;
  font-weight: 400;
  -webkit-animation: Move-header 2s ease-in-out 1s forwards;
          animation: Move-header 2s ease-in-out 1s forwards;
}

.App-header-subtext{
  margin-top: -2px;
  font-weight: 300;
  font-size: calc(5px + 2vmin);
  color: rgba(255,255,255, 0);
  -webkit-animation : Header-subtext-fadein 2s linear 3s forwards;
          animation : Header-subtext-fadein 2s linear 3s forwards;
}

.App-header-right{
  max-width : 70vh;
  max-height: 60px;
  display: flex;
  align-items: center;
  font-weight: 300;
  font-size: calc(5px + 2vmin);
  text-align: left;
  color: rgba(255,255,255, 0);
  /* border-left: solid lightgrey; */
  padding-left: 3vh;
  -webkit-animation : Header-subtext-fadein 2s ease-out 4s forwards;
          animation : Header-subtext-fadein 2s ease-out 4s forwards;
  postion: absolute;
}

.App-header-left{
  min-width: 70vh;
  position: in-block;
}


.App-link {
  color: #61dafb;
}

.Contact{
  position: fixed;
  top: 3%;
  right : -1%;
  background-color: PaleVioletRed;
  border-radius: 4px;
  font-size: 13px;
  padding: 10px 30px 10px 14px;
  color: white;
  border : none;
  font-weight: 900;
  cursor:pointer;
  transition: 1s;
}

.Contact:hover{
	-webkit-animation: gradient 5s ease infinite;
	        animation: gradient 5s ease infinite;
}

@-webkit-keyframes gradient {
  0%{
    background-color: PaleVioletRed;
  }
  25% {
		background:MediumOrchid;
	}
	50% {
    background: MediumPurple;
	}
	75% {
    background: thistle;
	}
  100%{
    background-color: PaleVioletRed;
  }
}

@keyframes gradient {
  0%{
    background-color: PaleVioletRed;
  }
  25% {
		background:MediumOrchid;
	}
	50% {
    background: MediumPurple;
	}
	75% {
    background: thistle;
	}
  100%{
    background-color: PaleVioletRed;
  }
}

@-webkit-keyframes Header-subtext-fadein{
  from {
    color: rgba(255,255,255, 0);
  }
  to {
    color: rgba(255,255,255, 1);
  }
}

@keyframes Header-subtext-fadein{
  from {
    color: rgba(255,255,255, 0);
  }
  to {
    color: rgba(255,255,255, 1);
  }
}

@-webkit-keyframes Move-header{
  from {
    font-size: calc(80px + 2vmin);
    min-height: 100vh;
  }
  to{
    font-size: calc(30px + 2vmin);
    min-height: 20vh;

  }
}

@keyframes Move-header{
  from {
    font-size: calc(80px + 2vmin);
    min-height: 100vh;
  }
  to{
    font-size: calc(30px + 2vmin);
    min-height: 20vh;

  }
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

